@import "../../_variable";

header {
  min-height: 100vh;
  max-height: fit-content;
  padding-bottom: 5rem;
}

.header__container {
  text-align: center;
  height: fit-content;
  position: relative;
  margin-top: 6rem;
  margin-bottom: 5rem;
  padding-bottom: 5rem;

  > h4 {
    color: $font-color-light;
    animation: opacity 2s;
  }

  > h1 {
    font-family: $font-kor;
    animation: opacity 2s forwards;
    opacity: 0;
    animation-delay: 0.5s;
  }

  > h3 {
    color: $accent-color;
    animation: opacity 2s forwards;
    animation-delay: 0.5s;
    opacity: 0;
  }
}

/* CTA */
.cta {
  margin-top: 2.5rem;
}

/* Header Socials */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: -50rem;
  bottom: 3rem;
  animation: left-bar 2.7s forwards ease-in-out;
}

@keyframes left-bar {
  from {
    left: -50rem;
  }
  to {
    left: 0rem;
  }
}

/* me */
.me {
  width: 22rem;
  height: 30rem;
  // position: absolute;
  // left: calc(50% - 11rem);
  margin: 0 auto;
  margin-top: 4rem;
  overflow: hidden;
  animation: opacity 2s forwards;
  animation-delay: 1s;
  opacity: 0;
}

/* scroll down */
.scroll__down {
  position: absolute;
  // right: -2.3rem;
  right: -50rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
  animation: right-bar 2.7s forwards ease-in-out;
}

@keyframes right-bar {
  from {
    right: -50rem;
  }
  to {
    right: -2.3rem;
  }
}

/* 미디어 쿼리 (medium) */
@media screen and (max-width: 1024px) {
}

@media screen and (max-width: 708px) {
  .header__container {
    > h1 {
      font-size: 2rem;
      line-height: 3rem;
      margin-top: 1rem;
      margin-bottom: 1rem;
    }
  }
}

/* 미디어 쿼리 (small) */
@media screen and (max-width: 600px) {
  .header__container {
    padding-bottom: 0;
  }
  .header__socials,
  .scroll__down {
    display: none;
  }
}
